export default {
  data() {
    return {
      // 手机号码输入的内容
      telVal: "",
      // 验证码输入的内容
      verificationVal: ""
    };
  },
  methods: {
    // 去设置新密码页面
    goLoginChangePassword() {
      if (this.telVal !== "" && this.verificationVal) {
        return uni.navigateTo({
          url: "/pages/loginChangePassword/loginChangePassword"
        });
      }
      return uni.showToast({
        title: "请输入手机号码或验证码",
        duration: 2000,
        icon: "none"
      });
    }
  },
  computed: {
    isActive() {
      if (this.telVal === "" || this.verificationVal === "") {
        return false;
      }
      return true;
    }
  }
};
